.con-todolist{
	width:40%;
	height: auto;
	margin:5% 30% 0;
}

.con-todolist input{
	height:38px;
	width:70%;
	border:1px solid #ccc;
	border-radius:5px;
	text-indent: 5px;
	font-size: 16px;
}

.con-todolist input:hover{border-color: #343434;}

.con-todolist a{
	display: inline-block;
	text-decoration: none;
	color:black;
	width:25%;
	height:40px;
	line-height: 40px;
	text-align: center;
	background:#ee9966;
	margin-left:4.5%;
}


